@import url(https://fonts.googleapis.com/css2?family=Caveat+Brush&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

*{
    margin: 0;
    box-sizing: border-box;
}
body{
    background-color: #1E2931;
}
h1{
    display: block;
    font-family: 'Caveat Brush', cursive;
    font-weight: 1000;
    font-size: 80px;
    color: rgb(0, 255, 55);
    margin-right: 1rem;
    margin-top: 2%;
}
h3{
    font-family: 'Caveat Brush', cursive;
    color: rgb(0, 255, 213);
    font-size: 2rem;
    margin: 0 auto;
    letter-spacing: 1px;
}
#Movies_container__ZlHUf{
    width: 80%;
    margin: 0 auto;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    padding: 1rem;
    justify-content: center;
}
.Movies_card__Cj2-B{
    width: calc(20% - 1rem);
    margin: 0.5rem;
    border-radius: 10px;
    border: 1px solid transparent;
    box-shadow: 0 0 4px rgb(255, 0, 76);
    cursor: pointer;
}
.Movies_card__Cj2-B:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 0 0 20px rgb(255, 115, 0);
    border: 1px solid rgb(228, 103, 54);
}
.Movies_card__Cj2-B img{
    width: 100%;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}
.Movies_card__Cj2-B p{
    color: white;
    padding: 5px;
}
#Movies_input__1BXy6{
    width: 30%;
    margin:10px auto;
    padding: 10px;
    border-radius: 8px;
    border: 1px solid grey;
    background-color: #1E2931;
    caret-color: white;
    font-size: 1rem;
    letter-spacing: 2px;
    color: #fff;;
}
footer{
    background-color: black;
    color: white;
    padding: 1rem;
    font-size: 1rem;
}
footer i{
    color: white;
    margin: 0 0.5rem;
}
@media (max-width:400px){
    #Movies_input-wrapper__yxyZA{
        display: flex;
        flex-direction: column;
    }
    h1,#Movies_input__1BXy6{
        width: 80%;
    }
    .Movies_card__Cj2-B{
        width: 40%;
    }
}
